<template>
 <div class="card mt-5 d-flex justify-content-center">
  <div class="card-body">
    <div class="container-fluid text-center">
      <h2 class="fs-2 mt-3 mb-4">Quote Generator</h2>

      <div class="form-group">
        <button type="button" class="btn btn-primary form-control" @click="GenerateQoute">Generate</button>
      </div>

      <div class="container mt-4" id="generator">
        <!-- The generated quote content will go here -->
      </div>
    </div>
  </div>
</div>

</template>

<script setup>
// eslint-disable-next-line no-unused-vars
const GenerateQoute = async()=>{
  const generator = document.querySelector('#generator');
 let randomNumber = Math.floor(Math.random() * 16);
await fetch("https://type.fit/api/quotes")
.then(function(response) {
    return response.json();
  })
  .then(function(data) {
    generator.innerHTML = `
    <div class="card text-center mt-5">
  <div class="card-body">
    <h4 class="card-title">Quote Number #${randomNumber}</h4>
    <p class="card-text">${data[randomNumber].text}</p>
  </div>
</div>
      `
  });
}
</script>

